<script>
import Action from "./Action.svelte"

export let doc

let full = true

function toggleOpen() {
  full = !full
}
</script>

<div class="feature {doc.type}">
  <h3 class="collapsible {full ? 'open' : 'closed'}"  on:click={toggleOpen}>{doc.name} ({doc.type})</h3>
  {#if full} <div class="actions">
    {#each doc.actions as action}
      <Action bind:doc={action}/>
    {/each}
  </div> {/if}
</div>

<style>
  h3 {
    font-size: 1.3em;
    font-weight: 50;
    padding: 3px 10px;
  }

  div.actions {
    display: flex;
    flex-flow: row wrap;
    height: min;
  }
</style>
