<script>
import Feature from "./Feature.svelte"
import yaml from "js-yaml"

let features = ""
fetch("./data/features.yaml").then(response => {
	return response.text()
}).then(response => {
	features=yaml.load(response)
})

let showing="features";

function show(section) {
	if (section !== showing) {
		showing = section
	} else {
		showing = ""
	}
}

</script>

<main>
	<h1>DRAGONSTEEL</h1>
	<p>Built with Ersatz</p>

	<h2 class="collapsible {showing === 'features' ? 'open' : 'closed'}" on:click={() => show('features')}>Arcana and Disciplines</h2>
	<div class="content">
		{#each features as feature}
			<Feature bind:doc={feature}/>
		{/each}
	</div>
</main>

